.jcc-alpha-marker {
  @include u-text(warning);
  text-transform: uppercase;
  font-size: 24px;
  font-family: sans-serif;
  vertical-align: super;
}

.filter-input-container {
  .srl-filter-input {
    background-position-y: 4px;
    background-position-x: 0.3rem;
    background-size: 1.8rem;
    background-image: url(../images/icomoon/SVG/search.svg);
    background-repeat: no-repeat;
    padding-left: 2.4rem;
  }
}

.search-results {
  background: white;
  border-radius: 4px;
  margin-top: 20px;
  color: #333;
  width: 800px;
  max-width: 100%;

  .results-header {
    padding: 10px 20px;
    border-bottom: 1px solid #eee;
    @include u-text(bold);
    @include u-text(primary);
    @include u-font(sans, md);
  }

  .guide-results {
    .guide-result-row {
      display: flex;

      .guide-result {
        flex-grow: 1;
        width: 50%;
        box-sizing: border-box;

        &:first-child {
          border-right: 1px solid #eee;
        }

        a {
          display: block;
          text-decoration: none;
          padding: 10px 20px;
          border-bottom: 1px solid #eee;
          color: #333;

          &:hover {
            text-decoration: underline;
          }
        }
      }
    }

    .no-results,
    .loading {
      padding: 10px 20px;
      border-bottom: 1px solid #eee;
      text-align: center;
    }
  }

  .form-results {
    .form-result {
      border-bottom: 1px solid #eee;

      &:last-child {
        border-bottom: none;
      }

      .form-result-content {
        display: flex;
        align-items: center;
        padding: 10px 20px;
        cursor: pointer;

        .form-number-and-title {
          flex-grow: 1;
          text-decoration: none;
          margin-right: 20px;
          color: #333;

          .form-number {
            text-transform: uppercase;
            color: #333;
            font-size: 16px;
          }

          .form-title {
          }

          &:hover {
            .form-title {
              text-decoration: underline;
            }
          }
        }

        .form-guide-button,
        .download-form-button {
          white-space: nowrap;
        }

        .form-guide-button {
          margin-right: 14px;
        }

        .download-form-button {
          margin-right: 0px;
        }
      }

      .form-links {
        display: none;
        padding: 4px 20px 10px 20px;

        &.visible {
          display: block;
        }
      }
    }

    .no-results,
    .loading {
      padding: 10px 20px;
      text-align: center;
    }
  }
}
